import ScrollReveal from 'scrollreveal'; 
import AOS from 'aos';
import particles from './particles';
import Color from './Color';

ScrollReveal().reveal('#particleCanvas', { duration: 1000, delay: 500 });
AOS.init();

export const myColor = new Color({o: .1});
console.log(myColor.rgb());

window.onload = () => {
  const sections = document.querySelectorAll('section');

  document.body.style.color = myColor.rgb();
  sections.forEach(sect => {
    sect.style.backgroundColor = myColor.rgba();
  });
}

