export default class {
  constructor(input) {
    this.r = input.r || Math.floor(Math.random() * 256);
    this.g = input.g || Math.floor(Math.random() * 256);
    this.b = input.b || Math.floor(Math.random() * 256);
    this.o = input.o || 1;
  }

  rgb() {
    const r = this.r;
    const g = this.g;
    const b = this.b;
    return `rgb(${r},${g},${b})`;
  }

  rgba() {
    const r = this.r;
    const g = this.g;
    const b = this.b;
    const o = this.o;
    return `rgba(${r},${g},${b},${o})`;
  }

  hex() {
    const r = this.r;
    const g = this.g;
    const b = this.b;
    return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
  }

  rgbObj() {
    const r = this.r;
    const g = this.g;
    const b = this.b;
    return {r,g,b};
  }
}